.blog,
.archive.category {
    .site-main {
        margin-top: 2rem;

        a {
            color: #4f4f4f;
        }
    }


    .site-main article {
        margin: 1rem;
        margin-bottom: 2rem;
        padding-bottom: 1rem;
        background-color: #f5f5f5;

        @include mq(laptop) {
            padding-bottom: 0;
        }

        .date {
            padding: 1rem 0;
            margin: 0;
            font-size: smaller;
        }

        a {
            text-decoration: none;


            @include mq(laptop) {
                display: flex;
                flex-direction: row;
            }

            hr {
                width: 25px;
                margin: 0;
                height: 2px;
            }

            .entry-content {
                padding: 0 1rem;
                margin: 0;
            }

        }

        .entry-header {

            @include mq(laptop) {
                max-width: 320px;
                max-height: 260px;
            }
        }

        img {
            width: 100%;
            height: 15rem;
            object-fit: cover;
            object-position: center 25%;

            @include mq(laptop) {
                height: 100%;
            }
        }


    }
}

.widget-area {

    padding: 1rem;

    .wp-block-latest-posts__list.wp-block-latest-posts {
        padding-left: 40px;
        list-style: disc;
    }

    a {
        // text-decoration: none;
    }

    ul {
        margin: 0;
    }

    ol {
        padding: 0;
    }

    li::marker {
        color: #179EC8;
    }

    .widget_sticky_posts {
        ul {
            padding: 0;
            list-style: none;
        }

        li {
            border-bottom: #4f4f4f 1px solid;
            padding-top: 24px;
        }

        img {
            width: 100%;
            height: 120px;
            object-fit: cover;
            object-position: center 25%;
            margin-bottom: 24px;
        }

        h3 {
            margin: 0;
        }

    }

    .widget_recent_entries {
        a:hover {
            text-decoration: underline;
        }
    }
}

.cotent-container .nav-links a,
.form-submit input {
    text-decoration: none;
    background-color: #fff;
    border-radius: 20px;
    border: solid 1px #06547E;
    color: #06547E;
    margin-bottom: 1rem;
    padding: 0.5rem 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.5rem;
    cursor: pointer;
}

.cotent-container .nav-links a:hover,
.form-submit input:hover {
    background: #06547E;
    color: #fff;
    transition: all 0.3s ease-in-out;
}

.cotent-container {
    .entry-meta {
        display: flex;
        flex-wrap: wrap;
        margin-bottom: 2rem;
    }

    .nav-links {
        margin-top: 2rem;
        gap: 1.5rem;
    }

    .wp-block-list {
        margin: 0;
    }

